import { Promise } from 'es6-promise'

// const lemmaCache = Object.create(null)

export default {
  getDictionary(language, page) {
    return this.get('/dictionaries/' + language + '/lemmas?page=' + page)
  },

  getLemma(language, lemma) {
    // return new Promise((resolve, reject) => {
    //   if (lemmaCache[id]) {
    //     resolve(lemmaCache[id])
    //   } else {
    //     api.child('item/' + id).once('value', snapshot => {
    //       const story = itemsCache[id] = snapshot.val()
    //       resolve(story)
    //     }, reject)
    //   }
    // })

    return this.get('/dictionaries/' + language + '/lemmas/' + lemma)
  },

  getTokens(language, lemma, pos, page) {
    return this.get('/tokens?language=' + language + '&lemma=' + lemma + '&part_of_speech=' + pos + '&page=' + page)
  },

  get(path) {
    let url = 'http://foni.uio.no:9292/api/v1/torot/20160616' + path

    return new Promise(function(resolve, reject) {
      var req = new XMLHttpRequest()
      req.open('GET', url)

      req.onload = function() {
        if (req.status === 200) {
          resolve(JSON.parse(req.response))
        } else {
          reject(Error(req.statusText))
        }
      }

      req.onerror = function() {
        reject(Error("Network Error"))
      }

      req.send()
    })
  }
}



