<template>
  <div>
    <h1 class="title" language="{{ lemma.language }}">{{ lemma.lemma }}</h1>
    <h2 class="subtitle">{{ lemma.language | humanizeLanguage }}, {{ lemma.part_of_speech | humanizePartOfSpeech }}</h2>

    <tabs type="boxed">
      <tab-pane label="Definition">
        <div class="box">
        <div class="content" :lang="$key" v-for="gloss in lemma.glosses">
          {{ gloss }}
        </div>
        </div>
      </tab-pane>

      <tab-pane label="Concordance">
        <concordance :tokens="tokens" :total="total" :on-change="changePage"></concordance>
      </tab-pane>

      <tab-pane label="Paradigm">
        <paradigm :paradigm="paradigm" :forms="forms"></paradigm>
      </tab-pane>

      <tab-pane label="Timeline">
        <timeline :timeline="timeline">
      </timeline>
    </tabs>

    <!-- h3 class="subtitle is-7">Homographs</h3>

