<template>
  <div>
    <section>
      <table class="table">
        <tbody>
          <tr v-for="token in tokens">
            <td><a href="#">{{ token.citation }}</a></td>
            <td class="has-text-right">{{ token.abbrev_text_before }}</td>
            <td class="highlight has-text-centered">{{ token.form }}</td>
            <td class="has-text-left">{{ token.abbrev_text_after }}</td>
          </tr>
        </tbody>
      </table>
    </section>

    <pagination :per-page="20" :on-change="onChange" :total="total"></pagination>
  </div>
</template>

<script>
import Pagination from './Pagination'

export default {
  components: {
    Pagination
  },

  props: {
    tokens: Array,
    total: Number,
    onChange: Function
  }
}
</script>

<style lang="sass" scoped>
