<template>
  <div>
    <nav class="pagination">
      <ul>
        <li>
          <a :class="{'is-disabled': currentPage == 1}" class="button" @click='onPrevious'>Previous</a>
        </li>
        <li>
          Page {{ currentPage }} of {{ totalPage }}
        </li>
        <li>
          <a :class="{'is-disabled': currentPage == totalPage}" class="button" @click='onNext'>Next page</a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentPage: 1
    }
  },

  props: {
    total: {
      type: Number,
      default() {
        return 0
      }
    },
    perPage: {
      type: Number,
      default() {
        return 20
      }
    },
    onChange: {
      type: Function
    }
  },

  computed: {
    totalPage() {
      return Math.ceil(this.total / this.perPage)
    }
  },

  watch: {
    currentPage(val) {
      if (this.onChange) {
        this.onChange(val)
      }
    }
  },

  methods: {
    onPrevious() {
      if (this.currentPage > 1) {
        this.currentPage--
      }
    },
    onNext() {
      if (this.currentPage < this.totalPage) {
        this.currentPage++
      }
    }
  }
}
</script>



