export default {
  humanizePartOfSpeech: function(store, partOfSpeech) {
    let schema = store.state.schema

    if (partOfSpeech && schema)
      return schema.part_of_speech[partOfSpeech]
    else
      return ''
  },

  humanizeLanguage: function(store, language) {
    let dictionaries = store.state.dictionaries

    if (language && dictionaries) {
      for (let i in dictionaries) {
        var d = dictionaries[i]
        if (d.id === language)
          return d.name
      }
      return ''
    } else
      return ''
  }
}



