<template>
  <div>
    <h1 class="title">Dictionary</h1>
    <h2 class="subtitle">{{ language | humanizeLanguage }}</h2>

    <table class="table">
      <tbody>
        <tr v-for="lemma in dictionary">
          <td><a v-link="{ name: 'lemma', params: { language: language, lemma: lemma.id }}">{{ lemma.lemma }} ({{ lemma.part_of_speech | humanizePartOfSpeech }})</a></td>
        </tr>
      </tbody>
    </table>

    <pagination :per-page="20" :on-change="changePage" :total="total"></pagination>

    <!-- v-server-table url='http://localhost:9292/api/v1/torot/20160616/dictionaries/orv/lemmas' :columns="columns" :options="options"></v-server-table -->

    <!-- v-paginator :resource.sync="dictionary" :resource_url="resource_url"></v-paginator -->
  </div>
</template>

<script>
import ProielService from './services/proiel'
import Schema from './schema'
import Pagination from './ui/Pagination'
// import VuePaginator from 'vuejs-paginator'

export default {
  name: 'DictionaryView',

  components: {
    Pagination
    // VPaginator: VuePaginator
  },

  data () {
    return {
      total: null,
      language: null,
      dictionary: []
    }
  },

  created () {
    this.language = this.$route.params.language
    // this.fetchData()
  },

  watch: {
  },

  filters: {
    humanizePartOfSpeech: function(partOfSpeech) {
      return Schema.humanizePartOfSpeech(window.store, partOfSpeech)
    },

    humanizeLanguage: function(language) {
      return Schema.humanizeLanguage(window.store, language)
    }
  },

  route: {
    data ({ to }) {
      this.language = to.params.language
      this.changePage(1)
    }
  },

  methods: {
    changePage(page) {
      var self = this
      ProielService.getDictionary(this.language, +page).then(function(dictionary) {
        self.total = dictionary.total
        self.dictionary = dictionary.data
      })
    }
  }
}
</script>



